// ------------------------------------------ //
// Buttons
// ------------------------------------------ //

.btn {
	font-family: @font-family-core-sans-bd;
	letter-spacing: -.015em;
	word-spacing:   -.05em;
	display: inline-block;
	margin-bottom: 0; // For input.btn
	font-weight: @btn-font-weight;
	text-align: center;
	vertical-align: middle;
	touch-action: manipulation;
	cursor: pointer;
	background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
	border: 1px solid transparent;
	white-space: nowrap;
	.button-size(@padding-base-vertical; @padding-base-horizontal; @font-size-base; (@line-height-computed - .0625); @border-radius-small);
	.user-select(none);
	.box-shadow(0px 2px 2px 0px rgba(0,0,0, .15));
	text-shadow: -1px -1px 0 rgba(0,0,0, .2);
	min-height: (38 * @toRems);
	position: relative;
	

	&.btn-link,
	&.btn-more {
		@shadow: none !important;
		.box-shadow(@shadow);
		text-shadow: none !important;
	}

	&.btn-default {
		text-shadow: 0 1px 0 rgba(255,255,255, .5);
		.box-shadow(0px 2px 1px 0px rgba(0,0,0, .03));
	}

	&,
	&:active,
	&.active {
		&:focus,
		&.focus {
			.tab-focus();
		}
	}

	&:hover,
	&:focus,
	&.focus {
		color: @btn-default-color;
		text-decoration: none;
		.box-shadow(none);
	}

	&:active,
	&.active {
		outline: 0;
		background-image: none;
		.box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
	}

	&.disabled,
	&[disabled],
	fieldset[disabled] & {
		cursor: @cursor-disabled;
		pointer-events: none; // Future-proof disabling of clicks
		.opacity(.65);
		.box-shadow(none);
	}
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
	.button-variant(@btn-default-color; @btn-default-bg; @btn-default-border);
}
.btn-primary {
	.button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-border);
}
// Success appears as green
.btn-success {
	.button-variant(@btn-success-color; @btn-success-bg; @btn-success-border);
}
// Info appears as blue-green
.btn-info {
	.button-variant(@btn-info-color; @btn-info-bg; @btn-info-border);
}
// Warning appears as orange
.btn-warning {
	.button-variant(@btn-warning-color; @btn-warning-bg; @btn-warning-border);
}
// Danger and error appear as red
.btn-danger {
	.button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-border);
}
// Contextual buttons 
.btn-more {
	.button-variant(@btn-more-color; @btn-more-bg; @btn-more-border);
}


// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
	color: @color-link;
	font-family: @font-family-core-sans-re;
	font-weight: normal;
	border-radius: 0;

	&,
	&:active,
	&.active,
	&[disabled],
	fieldset[disabled] & {
		background-color: transparent;
		.box-shadow(none);
	}
	&,
	&:hover,
	&:focus,
	&:active {
		border-color: transparent;
		text-decoration: none;
	}
	&:hover,
	&:focus {
		color: @gray-darker;
		text-decoration: none;
		background-color: transparent;
	}
	&[disabled],
	fieldset[disabled] & {
		&:hover,
		&:focus {
			color: @btn-link-disabled-color;
			text-decoration: none;
		}
	}
}

.btn-more {
	.button-size(
		@padding-xs-vertical; 
		@padding-xs-horizontal; 
		@font-size-small; 
		(@line-height-computed - .4375); 
		@border-radius-small
	);
	min-height: (18 * @toRems);
}


// Button Sizes
// --------------------------------------------------

.btn-lg {
	// line-height: ensure even-numbered height of button next to large input
	.button-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @border-radius-large);
}
.btn-sm {
	// line-height: ensure proper height of button next to small input
	.button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @border-radius-small);
}
.btn-xs {
	.button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-small; @line-height-small; @border-radius-small);
}


// Block button
// --------------------------------------------------

.btn-block {
	display: block;
	width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
	margin-top: (6 * @toRems);
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.btn-block {
		width: 100%;
	}
}
