// Include font-face declaration
// ------------------------------------

.font-face("bored", @include-svg: true, @font-path); 	//bored font glyphs

// Icons base
// ---------------------------

.icon {
	font-weight:    normal;
	font-family:    @icons-font-family;
	font-variant:   normal;
	text-align: 	center;
	text-transform: none;
	line-height: 	1;
	display: 		inline-block;
	vertical-align: middle;
	position: 		relative;
	top: 			-1px;
	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
  	speak: none;
}

